
import React, { useState, useEffect } from 'react';
import '../../styles/shop.css';

const PriceRangeSlider = ({ 
  min = 0, 
  max = 2000, 
  initialMin = 10, 
  initialMax = 1200,
  step = 1,
  currency = 'USD',
  onChange
}) => {
  const [priceRange, setPriceRange] = useState({
    min: initialMin,
    max: initialMax
  });

  useEffect(() => {
    onChange && onChange(priceRange);
    updateRangeFill();
  }, [priceRange.min, priceRange.max]);

  const updateRangeFill = () => {
    const minPercent = ((priceRange.min - min) / (max - min)) * 100;
    const maxPercent = ((priceRange.max - min) / (max - min)) * 100;
    
    const fillElement = document.getElementById('rangeFill');
    if (fillElement) {
      fillElement.style.left = `${minPercent}%`;
      fillElement.style.right = `${100 - maxPercent}%`;
    }
  };

  const handleMinChange = (e) => {
    const value = Math.min(Number(e.target.value), priceRange.max - step);
    setPriceRange(prev => ({ ...prev, min: Math.max(min, value) }));
  };

  const handleMaxChange = (e) => {
    const value = Math.max(Number(e.target.value), priceRange.min + step);
    setPriceRange(prev => ({ ...prev, max: Math.min(max, value) }));
  };

  const handleTrackClick = (e) => {
    const track = e.currentTarget;
    const rect = track.getBoundingClientRect();
    const percent = (e.clientX - rect.left) / rect.width;
    const value = Math.round(min + percent * (max - min));
    
    const isCloserToMin = Math.abs(value - priceRange.min) < Math.abs(value - priceRange.max);
    
    if (isCloserToMin) {
      setPriceRange(prev => ({ ...prev, min: Math.min(value, prev.max - step) }));
    } else {
      setPriceRange(prev => ({ ...prev, max: Math.max(value, prev.min + step) }));
    }
  };

  return (
    <div className="range-wrap" style={{ marginTop: '12px' }}>
      <h4>Price ({currency})</h4>
      
      <div className="range-controls" style={{ marginTop: '8px' }}>
        <input
          type="number"
          min={min}
          max={priceRange.max - step}
          value={priceRange.min}
          onChange={handleMinChange}
          aria-label="Minimum price"
          className="price-input"
        />
        <div style={{ color: 'var(--muted)' }}>—</div>
        <input
          type="number"
          min={priceRange.min + step}
          max={max}
          value={priceRange.max}
          onChange={handleMaxChange}
          aria-label="Maximum price"
          className="price-input"
        />
      </div>

      <div className="double-range" style={{ marginTop: '10px' }}>
        <div 
          className="range-track" 
          aria-hidden="true"
          onClick={handleTrackClick}
        >
          <div className="fill" id="rangeFill"></div>
        </div>
      </div>
      
      <div style={{ marginTop: '8px' }} className="muted">
        Tip: click on track or use inputs above
      </div>
    </div>
  );
};

export default PriceRangeSlider;
