// components/shop/Filters.tsx (client)
'use client';
import '../../styles/shop.css';
import React, { useState } from 'react';
import PriceRangeSlider from './PriceRangeSlider';
import { Radio, Slider, Select, Button, InputNumber, Row, Col } from 'antd';

type Props = {
  level: string;
  status: string;
  setLevel: (v: string) => void;
  setStatus: (v: string) => void;
  minPrice: number;
  maxPrice: number;
  setMinPrice: (n: number) => void;
  setMaxPrice: (n: number) => void;
  sort: string;
  setSort: (s: string) => void;
  apply: () => void;
};

export default function Filters({
  level, status, setLevel, setStatus,
  minPrice, maxPrice, setMinPrice, setMaxPrice,
  sort, setSort, apply
}: Props) {
  const levelOptions = ['All', 'Collector', 'Premium', 'Cute'];
  const statusOptions = ['All', 'In Stock', 'Pre-orde', 'Cute'];
  const [selectedRange, setSelectedRange] = useState({ min: 10, max: 1200 });

  const handleRangeChange = (range) => {
    setSelectedRange(range);
    console.log('Selected price range:', range);
  };

  return (
    <div>
      <h4>Filters</h4>

      <div style={{ marginTop: 8 }}>
        <h4>Product Level</h4>
        <div className='option-list' id="levelOptions">
          {levelOptions.map(option => (
            <Button
              key={option}
              className={`chip ${level === option ? 'active' : ''}`}
              onClick={() => setLevel(option)}
            >
              {option}
            </Button>
          ))}
        </div>

      </div>

      <div style={{ marginTop: 12 }}>
        <h4>Product Status</h4>
        <div className='option-list' id="statusOptions">
          {statusOptions.map(option => (
            <Button
              key={option}
              className={`chip ${status === option ? 'active' : ''}`}
              onClick={() => setStatus(option)}
            >
              {option}
            </Button>
          ))}
        </div>
      </div>

      <div style={{ marginTop: 12 }}>
        <h4>Price (USD)</h4>
        <div className="demo-container">
          <PriceRangeSlider
            min={0}
            max={2000}
            initialMin={10}
            initialMax={1200}
            onChange={handleRangeChange}
          />
          
          <div className="selected-range" style={{ marginTop: '20px' }}>
            <h4>Selected Range</h4>
            <div className="range-display">
              ${selectedRange.min} - ${selectedRange.max}
            </div>
          </div>
        </div>
        <Row gutter={8} align="middle">
          <Col flex="120px"><InputNumber min={0} value={minPrice} onChange={(v) => setMinPrice(Number(v || 0))} style={{ width: '100%' }} /></Col>
          <Col flex="auto"><Slider range min={0} max={2000} value={[minPrice, maxPrice]} onChange={(vals) => { setMinPrice(vals[0]); setMaxPrice(vals[1]); }} /></Col>
          <Col flex="120px"><InputNumber min={0} value={maxPrice} onChange={(v) => setMaxPrice(Number(v || 0))} style={{ width: '100%' }} /></Col>
        </Row>
      </div>

      <div style={{ marginTop: 12 }}>
        <h4>Sort By</h4>
        <Select value={sort} onChange={(v) => setSort(v)} style={{ width: '100%' }}>
          <Select.Option value="new">Newest arrivals</Select.Option>
          <Select.Option value="release_asc">Release date ↑</Select.Option>
          <Select.Option value="release_desc">Release date ↓</Select.Option>
          <Select.Option value="sales_desc">Best selling</Select.Option>
          <Select.Option value="fav_desc">Most saved</Select.Option>
          <Select.Option value="price_asc">Price: low → high</Select.Option>
          <Select.Option value="price_desc">Price: high → low</Select.Option>
        </Select>
      </div>

      <div style={{ marginTop: 14 }}>
        <Button type="primary" block onClick={apply}>Apply filters</Button>
      </div>
    </div>
  );
}